@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Kannada:wght@100..900&display=swap');


/* <uniquifier>: Use a unique and descriptive class name
/ <weight>: Use a value from 100 to 900
*/
.noto-sans-kannada-glaubhanta {
  font-family: "Noto Sans Kannada", sans-serif;
  font-optical-sizing: "auto";
  font-weight: "300";
  font-style: "normal";
  font-variation-settings:    "width 100";
}

.bg-gradient-primary {
    background: #f894e2 linear-gradient(180deg, #f8a7cd, #f894e2 ) repeat-x !important; /*#007bff linear-gradient(180deg, #268fff, #007bff) */ 
    color: #fff;
  }
  .btn-primary {
    color: #fff;
    background-color: #f894e2;
    border-color: #f894e2;
    box-shadow: none;
}
/* SALES GRAPH  */
  .bg-gradient-info {
    /*background: #17a2b8 linear-gradient(180deg, #3ab0c3, #17a2b8) repeat-x !important;*/
    background: #eb81b1 linear-gradient(180deg, #eb8cb7, #f998e9) repeat-x !important;

  }
  .bg-info {
    background-color: #eb8cb9 !important;
}
/* SALES GRAPH  */
  .bg-gradient-success {
    background: #f09cf3 linear-gradient(180deg, #f590df, #f09cf3) repeat-x !important;
  }

  .btn-success {
    color: #fff;
    background-color:#f592e2 ; /* #28a745*/
    border-color:   #e68fe4; /*#28a745*/
    box-shadow: none;
}
/* card tool pie and chart graph button */
.card-tools > ul > li > a.nav-link {
  background-color:#f8def2;
} 
.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
  background-color:#830267;
}

[class*=sidebar-dark-] .sidebar a {
  color: #ed4494;
}